:root {
  --bs-font-sans-serif: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", "Liberation Sans", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --bs-body-font-family: var(--bs-font-sans-serif);
  --border-gradient-onyx: linear-gradient(to bottom right, hsl(0, 0%, 25%) 0%,hsla(0, 0%, 25%, 0) 30%,hsla(0, 0%, 100%, 0) 70%);
}


body {
  background-color: hsl(0, 0%, 7%);
  color: #ffffff;
  font-family: 'Poppins', sans-serif;
  padding : 0;
  -webkit-font-smoothing: antialiased;
  line-height: 0.5;
  font-weight: 300;
  font-size: 15.5px;
  overflow-x: hidden; 
}



.top, .right {
  border: 1px black solid;
  background-color: hsl(240, 2%, 12%);
  border-radius: 15px;
}


.right {
  height: 100%;
  box-sizing: border-box;
  transition: margin-top 0.3s; 
  border: 1px solid rgb(41, 41, 41);
}

.body:focus-within .right {
  margin-top: 0; /* Remove top margin when container or its descendants have focus */
}

.body {
  justify-content: center;
  align-items: center; /* Align items to the top */
  width: 940px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

@media only screen and (max-width: 988px) {
  .body {
    width: 96%;
  }
}

@media only screen and (max-width: 700px) {
  .top {
    height: 140px !important;
  }

  .pic img {
    width: 120px;
  }

  h3 {
    font-size: 21px !important;
  }

  p, li {
    font-size: 15px;
  }

  .icon {
    width: 40px !important;
    height: 40px !important;
    font-size: 21px !important;
    margin-right: 20px !important;
    margin-left: 2px !important;
  }

  .rightContent {
    padding: 20px !important;
  }
}

@media only screen and (max-width: 615px) {
  .top {
    height: 140px !important;
  }

  .pic img {
    width: 120px;
  }

  h3 {
    font-size: 21px !important;
  }

  p, li {
    font-size: 14.4px;
  }

  .icon {
    width: 42px !important;
    height: 42px !important;
    font-size: 19px !important;
    margin-right: 18px !important;
    margin-left: 4px !important;
  }

  .rightContent {
    padding: 25px !important;
  }
}

@media only screen and (max-width: 500px) {
  h3 {
    font-size: 19px !important;
  }

  p, li {
    font-size: 14.2px;
  }

  .icon {
    width: 37px !important;
    height: 37px !important;
    font-size: 18px !important;
    margin-right: 20px !important;
    margin-left: 6px !important;
  }

  .rightContent {
    padding: 20px !important;
  }

  .intro p {
    font-size: 12.5px;
  }
}

@media only screen and (max-width: 420px) {
  .top {
    height: 120px !important;
  }

  .pic img {
    width: 100px;
  }

  h3 {
    font-size: 17px !important;
  }

  p, li {
    font-size: 13px;
  }

  .icon {
    width: 35px !important;
    height: 35px !important;
    font-size: 17px !important;
    margin-right: 21px !important;
    margin-left: 6px !important;
  }

  .rightContent {
    padding: 15px !important;
  }

  #email {
    margin-top: 11px !important;
  }

  .intro p {
    font-size: 11px;
  }

}

@media only screen and (max-width: 370px) {
  p, li {
    font-size: 12px;
  }
}



.resume {
  max-width: 800px;
  margin: 0 auto;
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

header {
  text-align: center;
  margin-bottom: 40px;
}

header h1 {
  margin: 0;
  font-size: 2.5em;
}

header p {
  margin: 5px 0;
  color: #666;
}

.timeline {
  margin-top: 5px;
  position: relative;
  padding: 0;
  list-style: none;
}


.timeline::before {
  content: '';
  position: absolute;
  top: 25px;
  bottom: 25px;
  left: 23px;
  width: 0.5px;
  background: rgb(192, 190, 190);
}

.event {
  display: flex;
  align-items: center;
}

.circle {
  width: 10px; /* Set width in pixels */
  height: 10px; /* Set height in pixels */
  background: #5ba8f4;
  border-radius: 50%;
  position: absolute;
  align-self: flex-start;
  margin-top: 12.5px; /* Assuming you want a specific margin */
  border: 2px solid #e4dede;
  box-sizing: content-box;
  left: 16px; /* Assuming you want a specific left position */
}



.content {
  margin-left: 64px;
}

ul {
  list-style-type: circle;
  padding: 0px;
  margin-top: -8px;
  width: 100%;
}
ul li {
  position: relative;
  line-height: 1.5;
}


.icon {
  position: relative;
  width: 48px;
  height: 48px;
  border-radius: 12px;
  font-size: 23px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: -4px 8px 24px hsla(0, 0%, 0%, 0.7);
  background: var(--border-gradient-onyx);
  z-index: 1;
  margin-right: 13px
}

.iconContainer {
  display: flex;
  align-items: center;
  height: 100%;
}

.rightContent {
  padding: 30px;
}

 h3 {
   font-size: 24px;
   text-align: center;
   margin: 0;
 }

 .date {
  color:#A7C7E7;
  font-weight: 400;
 }

 
 .top {
  margin-left: 0;
  margin-right: 0;
  display: flex;
  height: 170px;
  align-items: center;
  margin-bottom: 21px;
  margin-top: 21px;
  border: 1px solid rgb(41, 41, 41);
 }

 .top div{
  display: block;

 }

 

img {
  width: 145px;
  height: auto;
  border-radius: 100px;
}

.intro, .links{
  display: flex;
  justify-content: center;
  align-items: center;
}

.intro {
  display: block;
  justify-content: center;
  align-items: center;
  margin-top: 7px;
}

.intro, .pic {
  padding-left: 30px;
  padding-right: 30px;
  width: 50%;
}

.pic {
  text-align: right;
}


#email {
  margin-top: 18px;
}

.intro div {
  display: flex;
  width: 100%;
}
.intro p {
  text-align: left;
  margin-top: 4px;
  height: 3px;
  margin-left: 10px;

}

.intro h3 {
  text-align: left;
}

.links a {
  font-size: 40px;
  color: white;
}

a {
  color: white;
  text-decoration: none;
}


a:hover {
  text-decoration: underline;
}
